import { ElNotification } from 'element-plus'

export const requestResultNoti = (result: any) => {
    switch (result.code) {
        case 200:
            ElNotification({
                title: 'Success',
                message: result.msg,
                type: 'success',
            })
            break
        default:
            ElNotification({
                title: 'Warning',
                message:result.msg,
                type: 'warning',
            })
    }

}