
import { FormulaData, FormulaInterface } from '@/type/formula';
import { defineComponent, reactive, toRefs,onMounted } from 'vue'
import { getFormulaList, saveFormula } from '@/request/api'
import { requestResultNoti } from '@/request/resultNotification'
import { deepClone } from '@/function/deepClone';

export default defineComponent({
    setup() {
        //数据（搜索条件，搜索结果）
        const data = reactive(new FormulaData())

        //展示状态标志符
        const showStatus = reactive({
            'editbox-isshow': false,
            'title': ''
        })
        const search = () => {
            getFormulaList(data.searchCondition).then(
                res => {
                    requestResultNoti(res)
                    data.searchResult = res.data

                })
        }
        const currentChange = (page: number) => {
            data.searchCondition.pageNo = page
            search()
        }

        const sizeChange = (pagesize: number) => {
            data.searchCondition.pageSize = pagesize
            search()
        }

        const createFormula = () => {
            data.currentFormula = {
                id: "",
                fname: "",
                type: "",
                content: "",
                fexplain: "",
            }
            showStatus['title'] = "新建公式"
            showStatus['editbox-isshow'] = true
        }
        const editClick = (row: FormulaInterface) => {

            data.currentFormula = deepClone(row)
            showStatus['title'] = "编辑公式"
            showStatus['editbox-isshow'] = true
        }

        const editConfirm = () => {
            showStatus['editbox-isshow'] = false
            saveFormula(data.currentFormula).then(
                res => {
                    requestResultNoti(res)
                }
            )
            search()

        }
        onMounted(async () => {
            //dom 挂载后
            console.log("*******onMounted******")
            search()

        })

        return {
            ...toRefs(data),
            search,
            currentChange,
            sizeChange,
            showStatus,
            createFormula,
            editClick,
            editConfirm
        }

    },
})
