export interface PoetryInterface {
    id: string
    author: string
    title: string
    tags: Array<string>
    paragraphs: Array<string>
    pexplain: string
    video: string
}

export interface PoetrySearchParam {
    author?: string
    title?: string
    tags?: string
    pageNo: number
    pageSize: number
}
interface PageResult {
    record: Array<PoetryInterface>
    pageSize: number
    pageNo: number
    total: number
}

export class PoetryData {
    searchCondition: PoetrySearchParam = {
        pageNo: 1,
        pageSize: 10
    }
    searchResult: PageResult = {
        record: [],
        pageSize: 10,
        pageNo: 1,
        total: 0,
    }

    currentPoetry: PoetryInterface = {
        id: "",
        author: "",
        title: "",
        tags: [],
        paragraphs: [],
        pexplain: "",
        video: ""
    }
}