// 找一个数的所有约数
export const divisors = (integer: number) => {
    const arr = new Array<number>();
    for (let i = 2; i <= Math.floor(integer / 2); ++i) { if (integer % i === 0) arr.push(i) }
    return arr;
};

export const maxdivisor = (a: number,b:number)=>{
    while (a!=b)
        {
            if (a>b)
            {
                a = a -b;
            }
            else
            {
                b = b - a;
            }
        }
    return a
}