
import { defineComponent,onMounted,reactive, watch } from 'vue'
import { get100RandomWord, get100RandomWordWithoutBank } from '@/function/copyBookGen'

export default defineComponent({
    props: {
        level: String,
        isRowOne: String,
        isCustom: String,
    },
    setup(props, context) {
        let data  = reactive(new Array<Array<string>>())

        const gendata = () => {
            if (props.level != "999") {
                data= get100RandomWord({
                    level: props.level as string,
                    isRowOne: props.isRowOne as string,
                    isCustom: props.isCustom as string
                })
            }
            else {
                data= get100RandomWordWithoutBank(props.isRowOne as string)
            }
            
            
        }
        gendata()
        
        
        onMounted(()=>{
            gendata()
        })

        watch(() => props.level, (newVal) => {
            context.emit('update:level', newVal)
        })

        return {
            data
        }
    }
})
