
import { defineComponent, reactive, toRefs } from 'vue'
import { getQuestionList, saveQuestion } from '@/request/api';
import { QuestionQueryer, QuestionInterface, Question } from '../type/questionQueryer'
import {requestResultNoti} from '@/request/resultNotification'
export default defineComponent({
    setup() {
        const queryer = reactive(new QuestionQueryer())
        const showStatus = reactive(
            {
                "createbox-isshow": false,
                "editbox-isshow": false,
                "deletebox-isshow": false,
                "title": "新建问题"
            }
        )
        // 当前选中的对象
        const currentQuestion = reactive(
            {
                data: new Question().question
            }

        )
        console.log(currentQuestion.data);

        const createQuestion = ()=> {
            currentQuestion.data = new Question().question
            showStatus['title'] = "新建问题"
            showStatus['editbox-isshow'] = true
        }



        const editClick = (row: QuestionInterface) => {

            currentQuestion.data = row
            showStatus['title'] = "编辑问题"
            showStatus['editbox-isshow'] = true
        }


        const getlist = () => {
            getQuestionList(queryer.questionQueryer).then(
                res => {
                    requestResultNoti(res)
                    queryer.listResult = res.data.record
                    queryer.count = res.data.total
                })
        }

        const currentChange = (page: number) => {
            queryer.questionQueryer.pageNo = page
            getlist()
        }

        const sizeChange = (pagesize: number) => {
            queryer.questionQueryer.pageSize = pagesize
            getlist()
        }

        //编辑提交
        const editConfirm = () => {
            showStatus['editbox-isshow'] = false
            saveQuestion(currentQuestion.data).then(
                res =>{
                    requestResultNoti(res)
                }
            )
            getlist()
            
        }
        return {
            ...toRefs(queryer),
            getlist,
            currentChange,
            sizeChange,
            showStatus,
            currentQuestion,
            editClick,
            editConfirm,
            createQuestion
        }
    }
})
