
import { defineComponent, reactive, toRefs } from 'vue'
import { chickenRabbitQuestionGenerator } from '@/function/wordQuestionGenP1'

export default defineComponent({
    setup() {
        const data = reactive({
            drawerStatus: false,
            answerStatus: false,
            correctStatus: false,
            isCorrect: false,
            questiondata: {
                chickenNum: 0,
                rabbitNum: 0,
                headTotal: 0,
                footTotal: 0,
                question: ""
            },
            userAnswer: {
                suposeRabbitFoot: 0,
                suposeChickenFoot: 0,
                chickenNum: 0,
                rabbitNum: 0
            }
        })
        const generateAQuestion = () => {
            data.questiondata = chickenRabbitQuestionGenerator()
            data.answerStatus = true
            data.correctStatus = false
            data.isCorrect = false
        }
        const correct = () => {
            data.correctStatus = true
            if (data.questiondata.chickenNum == data.userAnswer.chickenNum && data.questiondata.rabbitNum == data.userAnswer.rabbitNum) {
                data.isCorrect = true
            }
        }
        const closeDrawer = () => {
            data.drawerStatus = false
            data.answerStatus = false
            data.correctStatus = false
            data.isCorrect = false
        }


        return {
            ...toRefs(data),
            generateAQuestion,
            closeDrawer,
            correct
        }
    }
})
