export interface FormulaInterface {
    id?: string
    fname: string
    type: string
    content: string
    fexplain: string
}
export interface FormulaSearchInterface {
    id?: string
    fname?: string
    type?: string
    content?: string
    fexplain?: string
    pageNo: number
    pageSize: number
}
export interface PageResult {
    record: Array<FormulaInterface>
    pageSize: number
    pageNo: number
    total: number
}


export class FormulaData {
    searchCondition: FormulaSearchInterface = {
        pageNo: 1,
        pageSize: 10

    }
    searchResult: PageResult = {
        record: [],
        pageSize: 1,
        pageNo: 10,
        total: 0
    }
    currentFormula: FormulaInterface = {
        id: "",
        fname: "",
        type: "",
        content: "",
        fexplain: "",
    }
}