export interface QuestionQueryerInterface {
    questionType?: string,
    questionTitle?: string,
    questionContent?: string,
    pageSize: number,
    pageNo: number,
}
export interface QuestionInterface {
    id?: string,
    title: string,
    type: string,
    content: string,
    answer: any,
    solve?:any,
    isCorrect?:boolean
}


export class QuestionQueryer {
    questionQueryer: QuestionQueryerInterface = {
        pageSize: 10,
        pageNo: 1,
    }
    listResult: Array<QuestionInterface> = []
    totalPage = 1
    count = 0
}

export class Question {
    question: QuestionInterface = {
        title: "",
        type: "",
        content: "",
        answer: ""
    }

}