
import { defineComponent, reactive, toRefs, ref } from 'vue'
import { GenData, FilterConditionInterface } from '@/type/questionGener'
import { QuestionInterface } from '@/type/questionQueryer'
import type { FormInstance } from 'element-plus'
import { random } from '@/function/random'
import { deepClone } from '@/function/deepClone'
import { divisors, maxdivisor } from '@/function/divisors'
import { exportSumData } from '@/request/api'
export default defineComponent({
    setup() {
        const ruleFormRef = ref<FormInstance>()

        const data = reactive(new GenData())
        let isClickCorrectButton = ref(false)
        let scoredata = reactive({
            score: 0,
            ismark: false
        })
        const rules = {
            type: [
                {
                    required: true,
                    message: "请选择问题类型",
                    trigger: "blur",
                }
            ],
            quantity: [
                {
                    required: true,
                    message: "请输入问题数量",
                    trigger: "blur",
                }
            ],
            numberRange: [
                {
                    required: true,
                    message: "请选择数字范围",
                    trigger: "blur",
                }
            ]
        }
        // 生成一个问题

        const generateAQuestion = (condition: FilterConditionInterface) => {
            const question: QuestionInterface = {
                title: "",
                type: "",
                content: "",
                answer: "",
            }
            let num1 = random(condition.numberRange[0], condition.numberRange[1])
            const num2 = random(condition.numberRange[0], condition.numberRange[1])
            let num3, num4, div, max_divisor
            switch (condition.type) {
                case "+":
                    question.content = num1 + "+" + num2;
                    question.answer = num1 + num2
                    break;
                case "-":
                    num3 = random(condition.numberRange[0], num1)
                    question.content = num1 + "-" + num3;
                    question.answer = num1 - num3
                    break;
                case "×":
                    question.content = num1 + "×" + num2;
                    question.answer = num1 * num2
                    break;
                case "÷":
                    num1 = random(1, condition.numberRange[1])
                    div = divisors(num1)
                    div.push(1)
                    div.push(num1)
                    console.log(div);

                    num3 = div[random(0, div.length - 1)]
                    question.content = num1 + "÷" + num3;
                    question.answer = num1 / num3
                    break;
                case "/+":
                    num3 = random(1, condition.numberRange[1])
                    num4 = random(1, condition.numberRange[1])
                    question.content = '\\frac{' + num1 + '}{' + num3 + '}' + '+' + '\\frac{' + num2 + '}{' + num4 + '}'
                    max_divisor = maxdivisor(num1 * num4 + num2 * num3, num3 * num4)
                    question.answer = (num1 * num4 + num2 * num3) / max_divisor + '/' + (num3 * num4) / max_divisor
                    break
                case "/-":
                    num3 = random(1, condition.numberRange[1])
                    num4 = random(1, condition.numberRange[1])
                    if (num1 * num4 > num2 * num3) {
                        question.content = '\\frac{' + num1 + '}{' + num3 + '}' + '-' + '\\frac{' + num2 + '}{' + num4 + '}'
                        max_divisor = maxdivisor(num1 * num4 + num2 * num3, num3 * num4)
                        if ((num3 * num4) / max_divisor == 1) {
                            question.answer = (num1 * num4 - num2 * num3) / max_divisor
                        }
                        else question.answer = (num1 * num4 - num2 * num3) / max_divisor + '/' + (num3 * num4) / max_divisor
                    }
                    else {
                        question.content = '\\frac{' + num2 + '}{' + num4 + '}' + '-' + '\\frac{' + num1 + '}{' + num3 + '}'
                        max_divisor = maxdivisor(num1 * num4 + num2 * num3, num3 * num4)
                        if ((num3 * num4) / max_divisor == 1) {
                            question.answer = (num2 * num3 - num1 * num4)
                        }
                        else question.answer = (num2 * num3 - num1 * num4) / max_divisor + '/' + (num3 * num4) / max_divisor
                    }

                    break
                default:
                    break

            }
            return question
        }


        //问题生成入口
        const genQuestions = (formEl: FormInstance | undefined) => {
            //清除判题展示
            isClickCorrectButton.value = false
            scoredata.ismark = false
            if (!formEl) return
            formEl.validate((valid) => {
                if (valid) {
                    //问题生成
                    const operator = ['+', "-", "/+", "/-"]
                    let filter = deepClone(data.filterdata)
                    data.generatedData = []

                    filter.numberRange = data.filterdata.numberRange.split(',') as Array<number>
                    for (var _i = 0; _i < data.filterdata.quantity; _i++) {
                        //混合运算处理
                        if (data.filterdata.type == '+-') {
                            filter.type = operator[random(0, 1)]
                        }
                        if (data.filterdata.type == '/') {
                            filter.type = operator[random(2, 3)]
                        }
                        data.generatedData.push(generateAQuestion(filter))
                    }
                } else {
                    console.log('error submit!')
                    return false
                }
            })


        }
        //判题函数
        const correctCal = () => {
            isClickCorrectButton.value = true
            let correct_count = 0
            data.generatedData.forEach((val, idx, array) => {
                if (val.answer == (val.solve as number)) {
                    val.isCorrect = true
                    correct_count++
                }
                else {
                    val.isCorrect = false
                }
            });
            scoredata.ismark = true
            scoredata.score = 5 * correct_count / data.filterdata.quantity

        }
        const exportQuestion = () => {
            exportSumData({
                "data": data.generatedData
            }).then(res => {
                console.log(res.data);
                
                let blob = new Blob([res.data], {
                    type: "application/vnd.ms-word;charset=utf-8",
                });
                let downloadElement = document.createElement("a");
                let href = window.URL.createObjectURL(blob); //创建下载的链接
                
                downloadElement.href = href;
                document.body.appendChild(downloadElement);
                downloadElement.download = "算术练习"+new Date().valueOf()+".doc"
                downloadElement.click(); //点击下载
                document.body.removeChild(downloadElement); //下载完成移除元素
                window.URL.revokeObjectURL(href); //释放掉blob对象
            })
                .catch((error) => {
                    console.log(error);
                });

        }
        return {
            ruleFormRef,
            ...toRefs(data),
            genQuestions,
            correctCal,
            isClickCorrectButton,
            rules,
            scoredata,
            exportQuestion
        }
    }
})
